.badge {
    background-color: #6394F8;
    border-radius: 10px;
    color: white;
    display: inline-block;
    font-size: 12px;
    line-height: 1;
    padding: 3px 7px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
}

.Cart{
  margin: 20px 0;
  float: right;
  background: white;
  width: 320px;
  position: absolute;
  border-radius: 20px;
  padding: 20px;
  right: 1%;
  top: 100px;
  animation: fadeIn 0.5s forwards;
}

@keyframes fadeIn {
  0% {
      opacity: 0;
    }
    100% {
      visibility: visible;
      opacity: 1;
    }
}


