.Cart:after {
	bottom: 100%;
	left: 89%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
	border-bottom-color: white;
	border-width: 8px;
	margin-left: -8px;
}

.cart-icon {
  color: #515783;
  font-size: 24px;
  margin-right: 7px;
  float: left;
}

.button {
  background: #6394F8;
  color:white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  display: block;
  border-radius: 3px;
  font-size: 16px;
  margin: 25px 0 15px 0;
  

}

.button:hover {
  background: lighten(#6394F8, 3%);
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.Cart .TotalPrice{
  border-style: solid none none none;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px 20px;

  
}
